// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import * as firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import 'animate.css/animate.min.css'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all.js'

Vue.config.productionTip = false

// 事件列車
window.EventBus = new Vue();

// 初始化 bootstrap for vue
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);

// 初始化 firebase 
const firebaseConfigOptions = {
  apiKey: 'AIzaSyCkMWErvatgd3cD5Qv6sDJhC1-Vwc2xL7U',
  authDomain: 'toseva-product-fast-search.firebaseapp.com',
  databaseURL: 'https://toseva-product-fast-search.firebaseio.com',
  projectId: 'toseva-product-fast-search',
  storageBucket: 'toseva-product-fast-search.appspot.com',
  messagingSenderId: '',
  appId: '1:454516942573:web:12b2f0d00892b41ade0ee4'
};

firebase.initializeApp(firebaseConfigOptions);

// 使用 google auth 進行登入身份驗證
router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
  if(requiresAuth && !currentUser)
  {
    next('/');
  }
  else if(requiresAuth && currentUser)
  {
    next();
  }
  else
  {
    next();
  }
});


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


// 跑完 npm run-script build 後，要記得把 =/static/ 改成 =./static/ ，不然 browser 會抓不到檔案


