<template>
    <div class="m-3">
        <img src="../assets/toseva_logo.png" class="img-fluid" />
        <h2>產品資料快速查詢</h2>
        <h3>Login</h3>
        <b-form v-on:submit.stop.prevent>
            
            <b-input type="email" v-model="userId" id="text-id" aria-describedby="id-help-block"></b-input>
            <b-form-text id="id-help-block">
                Please enter your login in
            </b-form-text>

            <b-input type="password" class="mt-3" v-model="userPw" id="text-password" aria-describedby="password-help-block"></b-input>
            <b-form-text id="password-help-block">
                Please enter your password
            </b-form-text>

            <b-button variant="success" class="mt-3" v-on:click="onsubmit"><span class="mr-3" v-if="loading"><i class="fas fa-circle-notch fa-spin"></i></span>Submit</b-button>
        </b-form>
    </div>
</template>

<script>

import firebase from 'firebase'

export default {
    name: "Login",
    data() {
        return {
            userId: "sales@toseva.com",
            userPw: "",  // toseva1234
            errorTxt: '',
            loading: false,
        };
    },
    methods: {
        onsubmit() {
            console.log("run onsubmit");
            console.log(this.userPw);
            console.log(this.userId);
            this.loading = true;

            firebase
                .auth()
                .signInWithEmailAndPassword(this.userId, this.userPw)
                .then(data=>{
                    this.$router.push("Main");
                })
                .catch(err=>{
                    this.errorTxt = err.message;
                    this.loading = false;
                    console.log(err);
                    if(err.code == 'auth/user-not-found')
                    {
                        alert('指定的帳號不存在');
                        this.$router.push("/");
                    }
                });

            // if(this.userPw=='1116')
            // {
            //     this.$router.push("Main");
            // }
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
